* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

body {
  font-family: helvetica, arial, sans-serif;
  padding: 0;
  margin: 0;
  position: fixed;
  background-color: #1A1C1D;
}

body, .full {
  width: 100%;
  height: 100%;
  min-height: 100%;
}

#main {
  overflow: auto;
}

.left-nav {
  height: 100%;
  width: 72px;
  position: absolute;
  background-color: #212325;
}
.right-content {
  padding-left: 72px;
}
@media (max-width: 768px) {
  .left-nav {
    display: none;
  }
  .right-content {
    padding-left: 0;
  }
}
.item-wrap {
  margin: 10px;
}
.item-wrap img {
  max-width: 100%;
}

.navbar.navbar-default {
  background: transparent;
  border: none;
}
.panel-default {
  border: none;
  background: transparent;
}
.panel-default > .panel-heading {
  background-color: #757272;
  border: none;
  color: white;
  text-transform: uppercase;
  border-radius: 0;
  -webkit-border-radius: 0;
}
.panel-body {
  padding: 35px;
}
.tab-content {
  margin: 50px 0 0 50px;
  width: 92%;
}
@media (max-width: 768px) {
  .tab-content {
    width: 86%;
  }
}
@media (max-width: 1024px) {
  .tab-content {
    width: 89%;
  }
}

h3 {
  color: white;
  margin: 0 15px 25px;
  font-weight: 300;
  padding-top: 30px;

}

#carousel-tabs .nav li:first-child {
  margin-left: 15px;
}
#carousel-tabs .nav li:last-child {
  margin-right: 0;
}
#carousel-tabs .nav li {
  width: 100px;
  margin-right: 3px;
  text-align: center;
  background-color: #212325;
}
#carousel-tabs .nav li > a {
  padding-top: 9px;
  margin-right: 0;
}
#carousel-tabs .nav li.active > a {
  padding-top: 10px;
}

li {
  margin-bottom: 5px;
}



